* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$whitecolor: #FFFFFF;
$greycolor: #202020;
$werucolor: #CCFF00;


body {
    font-family: 'Big Shoulders Display', cursive;
    color: $whitecolor;
}

// scrollbar style
::-webkit-scrollbar {
    width: 4px;
  }
::-webkit-scrollbar-track {
    background: transparent;

}
   
::-webkit-scrollbar-thumb {
    background: #414141;
}
  
::-webkit-scrollbar-thumb:hover {
    background: $werucolor; 
}



// ANIMATE BACKGROUND
.packed-background-group{
    top: 0;
    left: 0;
    position: fixed;
    overflow: hidden;
    background-color: $greycolor;
    height: 100vh;
    width: 100%;
    z-index: 100;
    .background-group {
        opacity: 0.5;
        top: 0;
        position: absolute;
        left: -290px;
        .group-svg {
            position: absolute;
            svg {
                position: absolute;
            }
            .group-svg-one {
                top: -8px;
            }
            .group-svg-two {
                top: -2px;
                left: 3px;
            }
            .group-svg-three {
                left: 6px;
            }
        }
    }
}

// CONTAINER
.container {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 200;

    /****************/
    /**** HEADER ****/
    /****************/
    header {
        width: 100%;
        height: 100%;
        display: grid;
        position: fixed;
        grid-template-columns: 1fr 1335px;
        grid-template-rows: 64px 1fr;
        grid-template-areas: 
        '. navigation'
        'article mainmain';


        /********NAVBAR********/
        .header-navbar {
            grid-area: navigation;
            display: flex;
            flex-flow: row wrap;
            justify-content: space-between;
            align-items: center;
            padding-right: 162px;
            .social-icons {
                display: flex;
                height: min-content;
                justify-content: space-between;
                width: 463px;
            }
            h2 {
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 100%;
                letter-spacing: 0.35em;
                color: $whitecolor;
            }
        }


        /******NAVIGATION******/
        .header-article {
            grid-area: article;
            display: flex;
            flex-flow: column wrap;
            align-items: center;
            .header-article-title {
                display: flex;
                flex-flow: column wrap;
                justify-content: space-between;
                align-items: center;
                height: 228px;
                margin-bottom: 140px;
                h1 {
                    font-style: normal;
                    font-weight: 500;
                    font-size: 34px;
                    line-height: 41px;
                    display: flex;
                    align-items: center;
                    text-align: center;
                    color: $werucolor;
                    flex: none;
                    order: 0;
                    flex-grow: 0;
                    margin: 8px 0px;
                }
            }
            .header-article-navigation {
                display: flex;
                flex-flow: column wrap;
                height: 218px;
                justify-content: space-between;
                a {
                    text-decoration: none;
                    h2 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 20px;
                        line-height: 100%;
                        display: flex;
                        align-items: center;
                        letter-spacing: 0.35em;
                        color: $whitecolor;
                    }
                }
            }
        }
    }

    /****************/
    /***** MAIN *****/
    /****************/

    main {
        grid-area: mainmain;
        background: rgba(51, 51, 51, 0.3);
        backdrop-filter: blur(1.6px);
        padding: 24px;
        padding-right: 0px;
        padding-top: 0;
        display: grid;
        grid-template-columns: 1fr 1fr 152px;
        grid-template-rows: 1fr 1fr;
        grid-row-gap: 24px;
        grid-column-gap: 24px;
        grid-template-areas: 
        '. . .'
        'img1 img2 .'
        'img3 img4 .'
        '. . .';
        overflow-y: scroll;
        .img1 {
            grid-area: img1;
        }
        .img2 {
            grid-area: img2;
        }
        .img3 {
            grid-area: img3;
        }
        .img4 {
            grid-area: img4;
        }
    }

}

